<template>
  <div class="box">
    <bgImg :bgImg="bgImage"></bgImg>
    <div class="module">
      <div class="title">
        
        <p class="line_lf"></p>
        <p class="line_rt"></p>
      </div>
      <div class="content">
        <div class="works" :style="{transform: transform}">
          <div class="work"></div>
          <div class="work"></div>
          <div class="work"></div>
          <div class="work"></div>
        </div>
        <div class="change_btn" @click="turn">
          <p class="turning" data-id="0" :class="{active: page === 0}"></p>
          <p class="turning" data-id="1" :class="{active: page === 1}"></p>
          <p class="turning" data-id="2" :class="{active: page === 2}"></p>
          <p class="turning" data-id="3" :class="{active: page === 3}"></p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import bgImg from './bgImg'

export default {
  name: 'myWorks',
  components: {
    bgImg
  },
  data () {
    return {
      page: 0,
      transform: undefined,
      bgImage: "url('./images/resume/bg-4.jpg')"
    }
  },
  methods: {
    turn (e) {
      if (e.target.nodeName === 'P') {
        this.page = parseInt(e.target.dataset.id)
        this.transform = `rotateY(${this.page * -90}deg)`
      }
    }
  }
}
</script>

<style scoped lang="less">
  @sm-width: 768px;
  @md-width: 992px;
  @lg-width: 1200px;
  @text-color-pc: #eee;
  @text-color-phone: #333;
  @btn-color: #258FB8;
  @active-color: #E5BF21;
  .box {
    position: relative;
    .module {
      text-align: center;
      @media screen and (min-width: @sm-width){
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -260px;
        margin-left: -360px;
        width: 720px;
        height: 480px;
      }
      @media screen and (max-width: @sm-width) {
        width: 100%;
      }
      .title {
        position: relative;
        display: inline-block;
        width: 30%;
        height: 40px;
        margin: 0 35%;
        font-size: 2em;
        letter-spacing: 4px;
        font-weight: bold;
        color: @text-color-pc;
        @media screen and (max-width: @sm-width) {
          color: @text-color-phone;
        }
        .line_lf, .line_rt {
          background-color: @text-color-pc;
          display: inline-block;
          margin: 0;
          width: 100%;
          height: 2px;
          position: absolute;
          top: 50%;
          @media screen and (max-width: @sm-width) {
            background-color: @text-color-phone;
          }
        }
        .line_lf {
          right: 100%;
        }
        .line_rt {
          left: 100%;
        }
      }
      .title:before, .title:after {
        position: absolute;
        top: 12px;
        background-color: @text-color-pc;
        content: '';
        display: inline-block;
        width: 18px;
        height: 18px;
        transform: rotateX(60deg) rotateZ(-45deg);
        @media screen and (max-width: @sm-width) {
          background-color: @text-color-phone;
        }
      }
      .title:before {
        right: 100%;
      }
      .title:after {
        left: 100%;
      }
      .content {
        width: 100%;
        height: 360px;
        position: relative;
        margin: auto;
        margin-top: 60px;
        perspective: 2000px;
        @media screen and (max-width: @sm-width) {
          width: 280px;
          margin-top: 30px;
        }
        .works {
          width: 600px;
          height: 300px;
          margin-left: 60px;
          position: relative;
          transform-style: preserve-3d;
          transition: transform .5s;
          @media screen and (max-width: @sm-width) {
            width: 240px;
            height: 300px;
            margin-left: 20px;
          }
          .work {
            position: absolute;
            width: 100%;
            height: 100%;
          }
          .work:nth-child(1) {
            background: rebeccapurple;
            transform: translateZ(300px);
            @media screen and (max-width: @sm-width) {
              transform: translateZ(120px);
            }
          }
          .work:nth-child(2) {
            background: chartreuse;
            transform: rotateY(90deg) translateZ(300px);
            @media screen and (max-width: @sm-width) {
              transform: rotateY(90deg) translateZ(120px);
            }
          }
          .work:nth-child(3) {
            background: aqua;
            transform: rotateY(180deg) translateZ(300px);
            @media screen and (max-width: @sm-width) {
              transform: rotateY(180deg) translateZ(120px);
            }
          }
          .work:nth-child(4) {
            background: bisque;
            transform: rotateY(270deg) translateZ(300px);
            @media screen and (max-width: @sm-width) {
              transform: rotateY(270deg) translateZ(120px);
            }
          }
        }
        .change_btn {
          position: absolute;
          z-index: 20;
          width: 100%;
          height: 40px;
          top: 92%;
          margin-top: -50px;
          @media screen and (max-width: @sm-width) {
            top: 100%;
          }
          .turning {
            background-color: @btn-color;
            display: inline-block;
            margin: 14px 10px;
            border: 1px solid #333;
            border-radius: 4px;
            width: 12px;
            height: 12px;
            transform: rotate(45deg);
            cursor: pointer;
          }
          .turning:hover {
            border: 1px solid @active-color;
          }
          .turning.active {
            background-color: @active-color;
          }
        }
      }
    }
  }
</style>
