<template>
  <div class="nav">
    <ul class="nav_bar" @click="changePage">
      <li class="nav_item" data-id="1" :class="{active:pageNum === 1}">
        <div><i class="icon">&#xe651;</i></div>
      </li>
      <li class="nav_item" data-id="2" :class="{active:pageNum === 2}">
        <div><i class="icon">&#xe667;</i></div>
      </li>
      <li class="nav_item" data-id="3" :class="{active:pageNum === 3}">
        <div><i class="icon">&#xe618;</i></div>
      </li>
      <li class="nav_item" data-id="4" :class="{active:pageNum === 4}">
        <div><i class="icon">&#xe625;</i></div>
      </li>
      <li class="nav_item" data-id="5" :class="{active:pageNum === 5}">
        <div><i class="icon">&#xe6e4;</i></div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'navBar',
  computed: {
    pageNum () {
      return this.$store.state.pageNum
    },
    isScrolling () {
      return this.$store.state.isScrolling
    }
  },
  methods: {
    changePage (e) {
      if (e.target.nodeName === 'LI') {
        let num = parseInt(e.target.dataset.id)
        if (num !== this.pageNum && !this.isScrolling) {
          this.$store.commit('changePages', num)
        }
      }
    }
  }
}
</script>

