<script>
import bgImg from './bgImg'

export default {
  name: 'mainInformation',
  components: {
    bgImg
  },
  data () {
    return {
      bgImage: "url('./images/resume/bg-1.jpg')",
      iconColor: [
        '#F07E27',
        '#64B41A',
        '#FA5D67',
        '#E5BF21',
        '#C115FA',
        '#0098FC'
      ],
      cubeImage: [
        'url("./images/resume/vue.png")',
        'url("./images/resume/css.png")',
        'url("./images/resume/html.png")',
        'url("./images/resume/php.png")',
        'url("./images/resume/javascript.png")'
      ],
      personalImage: [
        './images/resume/4.jpg',
        './images/resume/4.jpg',
        './images/resume/4.jpg'
      ]
    }
  },
  methods: {
    transformStart (e) {
      this.changeClass(e, e.target, 'in')
    },
    transformEnd (e) {
      this.changeClass(e, e.target, 'out')
    },
    changeClass (e, obj, state) {
      let direction = this.getDirection(e, obj)
      let animationName = direction + '_' + state
      obj.className = animationName
    },
    getDirection (e, obj) {
      let location = obj.getBoundingClientRect()
      let elCenter = [Math.round(location.x + location.width / 2), Math.round(location.y + location.height / 2)]
      if (e.clientX <= elCenter[0] && e.clientY <= elCenter[1]) {
        return 'lt'
      } else if (e.clientX > elCenter[0] && e.clientY <= elCenter[1]) {
        return 'rt'
      } else if (e.clientX <= elCenter[0] && e.clientY > elCenter[1]) {
        return 'lb'
      } else if (e.clientX > elCenter[0] && e.clientY > elCenter[1]) {
        return 'rb'
      }
    }
  }
}
</script>

<style scoped lang="less">
