  }
  .rb_in .cube {
    transform-origin: 100% 50%;
    animation: in-rb @animation-time ease forwards;
  }
  .lt_out .cube {
    transform-origin: 0% 50%;
    animation: out-lt @animation-time ease forwards;
  }
  .rt_out .cube {
