      opacity: 0;
    }
    to {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
  }
  @keyframes in-lb {
    from {
      transform: rotate3d(1, 0, 0, 90deg);
      opacity: 0;
    }
    to {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
  }
  @keyframes in-rb {
    from {
      transform: rotate3d(0, -1, 0, 90deg);
      opacity: 0;
    }
    to {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
  }
  @keyframes out-lt {
    from {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
    to {
      transform: rotate3d(0, 1, 0, @eye-deg);
      opacity: 0;
    }
  }
  @keyframes out-rt {
    from {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
    to {
      transform: rotate3d(-1, 0, 0, @eye-deg);
      opacity: 0;
    }
  }
  @keyframes out-lb {
    from {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
    to {
      transform: rotate3d(1, 0, 0, @eye-deg);
      opacity: 0;
    }
  }
  @keyframes out-rb {
    from {
      transform: rotate3d(0, 0, 0, 0deg);
      opacity: 1;
    }
    to {
      transform: rotate3d(0, -1, 0, @eye-deg);
      opacity: 0;
    }
  }
  /******/
