<template>
  <div class="profile">
    <profile-nav></profile-nav>
    <nav-bar></nav-bar>
    <div class="container" :style="move">
      <main-information></main-information>
      <about-me :class="{active: pageNum === 2}"></about-me>
      <my-skill :class="{active: pageNum === 3}"></my-skill>
      <my-works></my-works>
      <contact :class="{active: pageNum === 5}"></contact>
    </div>
  </div>
</template>

<script>
import navBar from '../components/profile/navBar'
import profileNav from '../components/profile/profileNav'
import mainInformation from '../components/profile/mainInformation'
import aboutMe from '../components/profile/aboutMe'
import mySkill from '../components/profile/mySkill'
import myWorks from '../components/profile/myWorks'
import contact from '../components/profile/contact'

export default {
  name: 'app',
  components: {
    navBar,
    profileNav,
    mainInformation,
    aboutMe,
    mySkill,
    myWorks,
    contact
  },
  data () {
    return {
      move: {
        transform: 'translateY(0px)',
        transition: undefined,
        timing: 'cubic-bezier(1,0,1,1)'// 
      }
    }
  },
  computed: {
    // store
    screenHeight () {
      return this.$store.state.screenHeight
    },
    screenWidth () {
      return this.$store.state.screenWidth
    },
    pageNum () {
      return this.$store.state.pageNum
    },
    isScrolling () {
      return this.$store.state.isScrolling
    },
    scrollTime () {
      return this.$store.state.scrollTime
    }
  },
  mounted () {
    // 
    window.onresize = () => {
      this.$store.commit('init')
    }
    // 
    if (window.addEventListener) {
      window.addEventListener('DOMMouseScroll', this.scroll, false)
    }
    window.onmousewheel = document.onmousewheel = this.scroll
  },
  watch: {
    // 
    pageNum: function (val) {
      if (this.screenWidth > 768) {
        this.move.transform = `translateY(${(val - 1) * this.screenHeight * -1}px)`
        this.move.transition = `transform ${this.scrollTime}ms ${this.move.timing}`
      } else {
        this.move.transform = 'translateY(0px)'
        this.move.transition = undefined
      }
    },
    // 
    screenHeight: function (val) {
      if (this.screenWidth > 768) {
        if (this.pageNum > 1) {
          this.move.transform = `translateY(${(this.pageNum - 1) * val * -1}px)`
          this.move.transition = undefined
        }
      } else {
        this.move.transform = 'translateY(0px)'
        this.move.transition = undefined
      }
    },
    // 
    screenWidth: function (val) {
      if (val < 768) {
        this.move.transform = 'translateY(0px)'
        this.move.transition = undefined
      } else {
        this.move.transform = `translateY(${(this.pageNum - 1) * this.screenHeight * -1}px)`
        this.move.transition = undefined
      }
    }
  },
  methods: {
    scroll (e) {
      window.onmousewheel = undefined // chrome
      e = e || window.event
      if (e.wheelDelta && this.screenWidth > 768) {
        if (e.wheelDelta > 0 && !this.isScrolling) {
          this.$store.commit('scrollTop')
        }
        if (e.wheelDelta < 0 && !this.isScrolling) {
          this.$store.commit('scrollBottom')
        }
      } else if (e.detail && this.screenWidth > 768) {
        if (e.detail > 0 && !this.isScrolling) {
          this.$store.commit('scrollTop')
        }
        if (e.detail < 0 && !this.isScrolling) {
          this.$store.commit('scrollBottom')
        }
      }
    }
  }
}
</script>

<style scoped lang="less">
  @lg-width: 1200px;
  @md-width: 992px;
  @sm-width: 768px;
  .profile {
    width: 100%;
    height: 100%;
    position: relative;
    @media screen and (min-width: @sm-width) {
      overflow: hidden;
    }
    .container {
      display: block;
      width: 100%;
      @media screen and (min-width: @sm-width) {
        height: 500%;
      }
      .box {
        display: block;
        width: 100%;
        overflow: hidden;
        @media screen and (min-width: @sm-width) {
          height: 20%;
        }
      }
    }
    .nav {
      position: fixed;
      top: 50%;
      right: 3%;
      width: 37px;
      height: 180px;
      z-index: 10;
      margin-top: -90px;
      @media screen and (max-width: @sm-width) {
        display: none;
      }
    }
  }
</style>
